/* Media Queries */
@mixin mq-xs {
    @media (min-width: 320px) {
      @content;
    }
  }
  @mixin mq-sm {
    @media (min-width: 480px) {
      @content;
    }
  }
  @mixin mq-md {
    @media (min-width: 720px) {
      @content;
    }
  }
  @mixin mq-lg {
    @media (min-width: 1000px) {
      @content;
    }
  }
  .rodape2 {
    background-color: rgba(237, 93, 52, 0.363);
    margin-bottom: 0%;
    padding: 0px;
    margin-top: 100px;
    text-align: center;
    font-size: 12px;
    font-family: 'Courier New', Courier, monospace;
    color: white;
  }

  $background: #12111400; // COR DE FUNDO
  $box-shadow: 0px 1px 22px 4px rgba(0, 0, 0, 0.07);
  $border: 1px solid rgba(191, 191, 191, 0);// borda ao redor do cartao e dividor de pagina
  $items: 7;
  $rows: ceil($items/2);
  /* TAMANHO DO CARDDDD */
  $card-height: 420px;
  $card-width: 440px;
  $inner-margin: 15px;
  $number-size: 20px;
  $stagger: 180px;
  $outer-margin: 90px;
  $marker-size: 20px;

  /* Colors --------------------- cores de fundo dos cards*/ 
  $steps: #12111469;
  $colors: #3c315aa1,
  #3c315aa1,
  #3c315aa1,
  #3c315aa1;
  $timeline: #d9dadd;
  /* Calculations */
  $container-height: $rows * ($card-height + $outer-margin) + $stagger;
  $container-width: $card-width*2 + $outer-margin*3;
  $head-height: $number-size + 50;
  $body-height: $card-height - $head-height;
  $marker-dist: $card-width + $outer-margin/2 - $marker-size/2;
  /* Placeholders */
   /* EFEITO DE DA IMAGEM DO CARD */
  .demo-card--step1 {
